import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl, Validators} from '@angular/forms'
import { GameServiceService } from '../../services/game-service.service';
import { Observable } from 'rxjs'
import { Router } from '@angular/router';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  username:string = ''
  NameForm:FormGroup
  name:FormControl
  constructor(private appService: GameServiceService, private router: Router) { }

  ngOnInit() {
    this.createNameFormControls()
    this.createNameForm()
  }

  createNameFormControls(){
    this.name = new FormControl('',[Validators.required, Validators.pattern('[a-zA-Z ]*')])
  }

  createNameForm(){
    this.NameForm = new FormGroup({
      gameName: this.name
    })
  }

  addName(form:any){
    if(this.NameForm.valid){
      this.username = this.name.value
      this.appService.setUserName(this.username)
      
      this.router.navigate(['name'])
    }
  }

  
}
