import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from '../app/home/home.component';
import { WelcomeComponent } from '../app/welcome/welcome.component'
import { GameComponent } from '../app/game/game.component'
import { GamenameComponent } from '../app/gamename/gamename.component';
import { ScoreComponent } from '../app/score/score.component';
import { GameGuard } from '../app/game.guard';

const routes: Routes = [
  {path:'', redirectTo:'home', pathMatch: 'full'},
  {path: 'home', component:HomeComponent},
  {path: 'name', component:WelcomeComponent, 
    children:[
      {path: '', component:GamenameComponent},
      {path: 'game', component:GameComponent},
      {path: 'score', component:ScoreComponent}
    ],
    canActivate:[GameGuard],
    canActivateChild:[GameGuard]
  },

  
];

@NgModule({
  imports: [RouterModule.forChild(routes),RouterModule.forRoot(routes)],
  exports: [RouterModule],

})
export class RoutingRoutingModule { }
