import { Injectable } from '@angular/core';
import { HttpModule, Headers, Response, Http} from '@angular/http'
import { AllItems } from '../models/allItems'
import { Observable } from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class GameServiceService {

  addedItem:AllItems[]
  username:string
  Totalscore: number
  isSet: boolean

  constructor(private http: Http) { 
    this.Totalscore = 0
    this.isSet = false
    this.username = ''
    this.addedItem =[]
  }

  addItem(value:AllItems){
    return this.http.post('items/add', value)
  }

  getItems(){
    return this.http.get('items')
  }

  deleteItem(value){
    return this.http.delete(`items/remove/${value}`)
  }

  setUserName(value:string){
    this.username = value
  }

  setScore(value:number){
    this.Totalscore += value
  }

  isSetUsername(): boolean{
    if(this.username === ''){ this.isSet = false }
    else{ this.isSet = true}
    
    return this.isSet
  }
}
