import { Component, OnInit } from '@angular/core';
import { AllItems } from '../../models/allItems';
import { GameServiceService } from '../../services/game-service.service';
import { map } from 'rxjs/operators'
import { Router } from '@angular/router';

@Component({
  selector: 'app-game',
  templateUrl: './game.component.html',
  styleUrls: ['./game.component.css']
})
export class GameComponent implements OnInit {

  addedItems: AllItems[]
  selectedItem: AllItems
  avatar:string[]

  dragData:string
  draggable:boolean

  fruitItem:AllItems[]
  vegetableItem:AllItems[]
  rootItem:AllItems[]

  rootScore:number
  vegetableScore:number
  fruitScore:number
  totalScore:number

  droppedCategory:string

  constructor(private GameService: GameServiceService, private router: Router) { 
    this.addedItems = []
    this.avatar = []
    this.draggable = true
    this.rootItem = []
    this.fruitItem = []
    this.vegetableItem = []

    this.rootScore = 0
    this.vegetableScore = 0
    this.fruitScore = 0
    this.totalScore = 0
  }

  ngOnInit() {
    this.GameService.getItems().pipe(
      map(res => res.json())
    )
    .subscribe(items =>{
      items.items.forEach(item =>{
        for(const space of item.item){
          if(space != ' '){
            this.avatar.push(space)
          }
        }
        
        this.addedItems.push({id: item._id, item: item.item, category: item.category, avatar:this.avatar.join('').toLowerCase() })
        // console.log(this.avatar.join('').toLowerCase())

        this.avatar.splice(0, this.avatar.length)
      })
    })
  }


  drag(e){
    e.dataTransfer.setData("text", e.target.id)
    // this.selectedItem = e.target.innerText
  }

  allowDrop(e){
    e.preventDefault()
  }

  drop(e){
    e.preventDefault()
    this.dragData = e.dataTransfer.getData("text")


    this.addedItems.forEach((id)=>{
      if(this.dragData === id.id){
        this.selectedItem = id
      }
    })

    this.droppedCategory = e.target.id


    if(this.droppedCategory === 'fruits'){
      this.fruitItem.push(this.selectedItem)
      this.splice(this.selectedItem)

    }
    else if(this.droppedCategory === 'vegetables'){
      this.vegetableItem.push(this.selectedItem)
      this.splice(this.selectedItem)
    }
    else if(this.droppedCategory === 'roots'){
      this.rootItem.push(this.selectedItem)
      this.splice(this.selectedItem)
      // console.log(this.selectedItem)
    }

    else{

    }


    if(this.addedItems.length === 0){
      this.roots()
      this.fruits()
      this.vegetables()
      this.scores()
      
      this.GameService.setScore(this.totalScore)
      this.router.navigate(['/name/score'])
    }
  }

  splice(value){
    this.addedItems.forEach((item, i) => {
      if(item.id === value.id){
        this.addedItems.splice(i, 1)
      }
    })
  }

  scores(){
    this.totalScore = Math.ceil(((this.rootScore + this.fruitScore + this.vegetableScore)/15) * 100)
  }

  roots(){
    this.rootItem.forEach((item)=>{
      if(item.category === 'Root'){
        this.rootScore += 1
      }
    })
  }

  fruits(){
    this.fruitItem.forEach((item)=>{
      if(item.category === 'Fruit'){
        this.fruitScore += 1
      }
    })
  }

  vegetables(){
    this.vegetableItem.forEach((item)=>{
      if(item.category === 'Vegetable'){
        this.vegetableScore += 1
      }
    })
  }

}
